import React from 'react'
import {HashRouter as Router, Route, Switch} from 'react-router-dom'
import asyncComponent from './AsyncComponent.js'

const Home = asyncComponent(() => import('../Home/Home.js'));
const AboutReact  = asyncComponent(() => import('../AboutReact/Component/AboutReact.js'));
const CreatReactWithTable  = asyncComponent(() => import('../AboutReact/Component/CreatReactWithTable.js'));
const PriceInput  = asyncComponent(() => import('../AboutReact/Component/PriceInput.js'));

const AboutRouter  = asyncComponent(() => import('../AboutReact/Method/AboutRouter.js'));
const AboutCss  = asyncComponent(() => import('../AboutReact/Method/AboutCss.js'));
const AboutKey  = asyncComponent(() => import('../AboutReact/Method/AboutKey.js'));
const AboutUrl  = asyncComponent(() => import('../AboutReact/Method/AboutUrl.js'));
const RouterClass = () => (
    <Router>
        <Switch>
            <Route exact path="/" component={AboutReact}/>
            <Route exact path="/aboutReact/base" component={AboutReact}/>
            <Route exact path="/aboutReact/table" component={CreatReactWithTable}/>
            <Route exact path="/aboutReact/priceInput" component={PriceInput}/>

            <Route exact path="/aboutReact/aboutRouter" component={AboutRouter}/>
            <Route exact path="/aboutReact/aboutCss" component={AboutCss}/>
            <Route exact path="/aboutReact/aboutKey" component={AboutKey}/>
            <Route exact path="/aboutReact/aboutUrl" component={AboutUrl}/>
        </Switch>
    </Router>
);
export default RouterClass
