import React, {Component} from 'react';
import { Layout, Menu, Breadcrumb,Icon } from 'antd';
import Router from "../Router/Router";
import './App.scss';
const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;


class App extends Component {
    constructor(props) {
        super(props);
        this.state={
          siderlist : [{title:'组件',submenu:[{title:'新建',ref:'aboutReact/base'},{title:'基本table',ref:'aboutReact/table'},{title:'PriceInput',ref:'aboutReact/priceInput'}]},
              {title:'常用方法',submenu:[{title:'路由相关',ref:'aboutReact/aboutRouter'},{title:'css相关',ref:'aboutReact/aboutCss'},
                      {title:'KEY相关',ref:'aboutReact/aboutKey'},
                      {title:'URL相关',ref:'aboutReact/aboutUrl'}]}],
          defaultSelectedKeys:['subItem00'],
          defaultOpenKeys:['sub0']
        };

    }

    componentWillMount() {

    }

    componentDidMount() {

    }

    changeMenuItem=(e)=>{
        switch (e.key) {
            case '1':
                this.setState({
                    siderlist : [{title:'新建',submenu:[{title:'基本',ref:'aboutReact/base'},{title:'基本table'}]},{title:'组件',submenu:[{title:'test1'},{title:'test2'}]}],
                    defaultSelectedKeys:['subItem00'],
                    defaultOpenKeys:['sub0']
                });
                // this.props.history.push(`orderManagerDetil`)
                break;
            case '2':
                this.setState({
                    siderlist : [{title:'mooncell',submenu:[{title:'ruler'},{title:'saber'}]},{title:'wiki',submenu:[{title:'ttt'},{title:'sd'}]}],
                    defaultSelectedKeys:['subItem00'],
                    defaultOpenKeys:['sub0']
                });
                break;
            case '3':

                break;
        }
    };

    renderSiderList(list){
        const {defaultSelectedKeys,defaultOpenKeys} = this.state;
      return  <Menu
            mode="inline"
            defaultSelectedKeys={defaultSelectedKeys}
            defaultOpenKeys={defaultOpenKeys}
            style={{ height: '100%', borderRight: 0 }}
        >
          {
              list.map((c,k)=>{
                return  <SubMenu
                    key={"sub"+k}
                    title={
                        <span>
                            { c.title}
                        </span>
                    }
                >
                    {
                        c.submenu && c.submenu.map((_c,_k)=>{
                           return <Menu.Item key={`subItem${k}${_k}`}>
                               {
                                   _c.ref?
                                       <a href={_c.ref?`#/${_c.ref}`:''}>{_c.title}</a>
                                       :
                                       <span>{_c.title}</span>
                               }

                               </Menu.Item>
                        })
                    }
                </SubMenu>
              })
          }
        </Menu>
    }

    render() {
        const {siderlist} = this.state;
        return (
            <div>
                <Header className='app-header'>
                    <Menu  mode="horizontal" defaultSelectedKeys={['1']} className='app-header-menu' onClick={this.changeMenuItem}>
                        <Menu.Item key="1" className='app-header-menu-item'><a href='#/aboutReact'>React</a></Menu.Item>
                        <Menu.Item key="2" className='app-header-menu-item'>nav 2</Menu.Item>
                        <Menu.Item key="3" className='app-header-menu-item'>nav 3</Menu.Item>
                    </Menu>
                </Header>
                <Layout>

                    <Sider width={200} className="site-layout-background">
                        {
                            this.renderSiderList(siderlist)
                        }
                    </Sider>

                    <Layout style={{ padding: '0 24px 24px' }}>
                        <Content
                            className="site-layout-background"
                            style={{
                                padding: 24,
                                margin: '24px 16px 0',
                                minHeight: 280,
                                height:"100%"
                            }}
                        >
                            <div style={{padding: 24, background: '#fff', minHeight: 360}}>
                                <Router/>
                            </div>
                        </Content>
                    </Layout>
                </Layout>
            </div>

        );
    }
}


export default App;
